@use "base/variables" as v;
@use "base/mixins" as m;

.hero-img {
    .img-container {
        img {
            width: 100%;
            height: 20rem;
            object-fit: cover;
            @include m.mobile {
                height: 30rem;
            }
            @include m.tablet {
                height: 40rem;
            }
            @include m.laptop {
                height: 50rem;
            }
            @include m.desktop {
                height: 60rem;
            }
            @include m.desktopXL {
                height: 90rem;
            }
        }
    }
}

.carousel-control-next-icon, .carousel-control-prev-icon {
    width: 1rem;
    @include m.tablet {
        width: 1.5rem;
    }
}

.carousel {
    [type=button]:not(:disabled), 
[type=reset]:not(:disabled), 
[type=submit]:not(:disabled), 
button:not(:disabled) {
    width: 2.5rem;
    height: 2.5rem;
    background-color: v.$primaryPinkDark;
    border-radius: 5rem;
    margin-top: 4rem;
    @include m.mobile {
        margin-top: 7rem;
    }
    @include m.tablet {
        margin-top: 10rem;
        width: 3rem;
        height: 3rem;
    }
    @include m.laptop {
        margin-top: 15rem;
    }
    @include m.desktopXL {
        margin-top: 25rem;
        width: 4.5rem;
        height: 4.5rem;
    }
}
}

.carousel-control-prev {
    margin-left: 1rem;
    @include m.tablet {
        margin-left: 2rem;
    }
    @include m.desktopXL {
        margin-left: 5rem;
    }
}

.carousel-control-next {
    margin-right: 1rem;
    @include m.tablet {
        margin-right: 2rem;
    }
    @include m.desktopXL {
        margin-right: 5rem;
    }
}